            color:rgba(51,51,51,1);
            white-space: pre-wrap;
        }
        #section_addPrice_box_content_table1>ul>li>input{
            width: 468px;
            height: 32px;
            background:rgba(255,255,255,1);
            border-radius:4px;
            border:1px solid rgba(0,0,0,0.15);
            padding-left: 12px;
            padding-top: 6px;
            padding-bottom: 6px;
        }
</style>



